.only-loading {
  align-items: center;
  background: #111;
  display: flex;
  font-family: Montserrat , sans-serif;
  height: 100vh;
  justify-content: center;
  min-height: 100vh;
  width: 100vw;
}

.loading span {
  animation: animate 1.5s linear infinite;
  color: #111;
  font-size: 3em;
  font-weight: 900;
  margin: 0 15px;
}

/* box-shadow: 10px 10px 10px 0px rgba(0, 0, 0, 0.76),-2px -2px 10px 0px rgba(167, 167, 167, 0.15); ( Saindo da tela ) */
/* box-shadow: inset 5px 5px 5px 0px rgba(0, 0, 0, 0.76), inset -2px -2px 4px 0px rgba(167, 167, 167, 0.15); ( Entrando na tela ) */

@keyframes animate {

  0% {
    text-shadow: -6px -6px 14px rgba(167, 167, 167, 0.15) , 6px 6px 10px rgba(0, 0, 0, 0.76);
    transform: scaleX(1);
  }

  40% {
    text-shadow: inset -6px -6px 14px rgba(167, 167, 167, 0.15) , inset 6px 6px 10px rgba(0, 0, 0, 0.76);
    transform: scale(1.4);
  }

  80%, 100% {
    text-shadow: inset -6px -6px 14px rgba(167, 167, 167, 0.15) , inset 6px 6px 10px rgba(0, 0, 0, 0.76);
    transform: scale(1);
  }
}

.loading span:nth-child( 1 ) {
  animation-delay: 0s;
  font-size: 3em;
}

.loading span:nth-child( 2 ) {
  animation-delay: 0.15s;
  font-size: 4em;
}

.loading span:nth-child( 3 ) {
  animation-delay: 0.3s;
  font-size: 5em;
}

.loading span:nth-child( 4 ) {
  animation-delay: 0.45s;
  font-size: 6em;
}

.loading span:nth-child( 5 ) {
  animation-delay: 0.6s;
  font-size: 7em;
}

.loading span:nth-child( 6 ) {
  animation-delay: 0.75s;
  font-size: 7em;
}

.loading span:nth-child( 7 ) {
  animation-delay: 0.9s;
  font-size: 6em;
}

.loading span:nth-child( 8 ) {
  animation-delay: 1.05s;
  font-size: 5em;
}

.loading span:nth-child( 9 ) {
  animation-delay: 1.2s;
  font-size: 4em;
}

.loading span:nth-child( 10 ) {
  animation-delay: 1.35s;
  font-size: 3em;
}

